import React, { useState } from "react";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import { useDispatch } from "react-redux";
import * as action from "redux/actions/actions";
import { useHistory } from "react-router-dom";
import { Modal } from "react-bootstrap";
import { serverCallPost } from "serverReq/axios";
import { validateEmail } from "utility/validations";
import { validatePassword } from "utility/validations";
import CircularProgress from '@material-ui/core/CircularProgress';


const useStyles = makeStyles(theme => ({
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center"
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(3)
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  },

}));

export default function SignUp() {
  const classes = useStyles();

  const dispatch = useDispatch();
  const [name, setName] = useState(" ");
  const [lastName, setLastName] = useState(" ");
  const [email, setEmail] = useState(null);
  const [password, setPassword] = useState(null);
  const [passwordConfirm, setPasswordConfirm] = useState(null);
  const [displayModal, setmodalDisplay] = useState(false);
  const [error, setError] = useState(false);
  const [passwordError, setPasswordError] = useState(null);
  const [emailError, setEmailError] = useState(null);
  const [load, setloader] = useState(false);

  const history = useHistory();

  const callbackSucss = response => {
    if (response) {
      if (response.status === 200) {
        dispatch(action.onLogin(response.data));
        setloader(false)
        history.push("/admin");
      }
    }
  };

  const callbackFailur = () => {
    setloader(false)
    setmodalDisplay(true);
    setError("Email is alredy exit");
  };

  const submitSignup = async () => {
    setloader(true)
    setEmailError(null);
    setPasswordError(null);
    if (
      validateEmail(email, setEmailError) &&
      validatePassword(password, passwordConfirm, setPasswordError)
    ) {
      serverCallPost(
        "signup",
        { name, lastName, email, password },
        callbackSucss,
        callbackFailur
      );
    }
  };
  if (!load) {
    return (
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <div className={classes.paper}>
          <Avatar className={classes.avatar}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Sign up
          </Typography>
          <form className={classes.form} noValidate>
            <Grid container spacing={2}>
              <Grid item xs={12} sm={6}>
                <TextField
                  autoComplete="fname"
                  name="firstName"
                  variant="outlined"
                  required
                  fullWidth
                  id="firstName"
                  label="First Name"
                  autoFocus
                  value={name}
                  onChange={e => {
                    setName(e.target.value);
                  }}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  variant="outlined"
                  required
                  fullWidth
                  id="lastName"
                  label="Last Name"
                  name="lastName"
                  autoComplete="lname"
                  value={lastName}
                  onChange={e => {
                    setLastName(e.target.value);
                  }}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  error={emailError ? true : false}
                  helperText={emailError ? emailError : null}
                  variant="outlined"
                  required
                  fullWidth
                  id="email"
                  label="Email Address"
                  name="email"
                  autoComplete="email"
                  value={email}
                  onChange={e => {
                    setEmail(e.target.value);
                  }}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  error={passwordError ? true : false}
                  helperText={passwordError ? passwordError : null}
                  variant="outlined"
                  required
                  fullWidth
                  name="password"
                  label="Password"
                  type="password"
                  id="password"
                  autoComplete="current-password"
                  value={password}
                  onChange={e => {
                    setPassword(e.target.value);
                  }}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  variant="outlined"
                  required
                  fullWidth
                  name="password confirm"
                  label="Password confirm"
                  type="password"
                  id="passwordconfirm"
                  autoComplete="current-password"
                  value={passwordConfirm}
                  onChange={e => {
                    setPasswordConfirm(e.target.value);
                  }}
                />
              </Grid>
            </Grid>
            <Button
              type="button"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
              onClick={submitSignup}
            >
              Sign Up
            </Button>
            <Grid container justifyContent="flex-end">
              <Grid item>
                <Link href="/login" variant="body2">
                  Already have an account? Sign in
                </Link>
              </Grid>
            </Grid>
          </form>
          <Modal show={displayModal} onHide={() => setmodalDisplay(false)}>
            <Modal.Header closeButton>
              <Modal.Title>Error</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <p>{error ? error : "Somthing went worng"}</p>
            </Modal.Body>
          </Modal>
        </div>
      </Container>
    );
  } else {

    return (
      <Container component="main"className={classes.paper} maxWidth="xs">
        <CssBaseline />
        <CircularProgress  disableShrink />
      </Container>
    )

  }

}
